  transition: 0.2s ease-in-out; }
  .pulse-loader:before, .pulse-loader:after {
    -webkit-animation: pulse-loader infinite 2s;
            animation: pulse-loader infinite 2s;
    box-shadow: 0 0 5px #bc9a36;
    border-radius: 50%;
    border: 3px solid #bc9a36;
    content: '';
